@charset "UTF-8";

@import "base";

//support
.support .content-box{min-height: 802px;}

.support .notice-table li + li{margin-top: 6px;}
.support .notice-table li a{@include flex(); padding: 12px 24px; font-size: 16px; border-radius: 12px;}
.support .notice-table li a:hover{background-color: $c-gray-2;}
.support .notice-table .tit{padding-right: 20px; font-weight: 500; color: $c-gray-3; line-height: 1.75;}
.support .notice-table .date{margin-left: auto; color: $c-gray-4; line-height: 1.25;}

.support .question .accordion{margin-bottom: 6px; padding: 0 24px;}
.support .question .accordion:last-child{margin-bottom: 0;}
.support .question .accordion.active{background-color: #f7f8fd; border-radius: 12px;}
.support .question .accordion.active .top{border-bottom: 1px solid $c-gray-6;}
.support .question .accordion.active .top button::after{transform: rotate(-180deg);}
.support .question .accordion .top button{@include flex(); position: relative; padding: 12px 0 12px 40px; width: 100%; font-size: 16px; line-height: 1.5;}
.support .question .accordion .top button::after{content: ''; display: inline-block; margin-left: auto; width: 24px; height: 24px; background-color: $c-gray-3; @include mask('common/icon_arrow_down.svg');}
.support .question .accordion .top button::before{content: ''; position: absolute; left: 0; top: 11px; width: 24px; height: 24px; background-color: $c-main; @include mask('common/icon_question.svg', $c-main)}
.support .question .accordion .top .kind{flex-shrink: 0; color: $c-gray-4; }
.support .question .accordion .top .kind::after{content: ''; display: inline-block; margin: -2px 12px 0; width: 1px; height: 18px; vertical-align: middle; background-color: $c-gray-6;}
.support .question .accordion .top .tit{font-weight: 500; color: $c-gray-3; text-align: left;}
.support .question .accordion .detail{padding: 20px 48px 24px 95px;}
.support .question .accordion .detail strong{display: inline-block; margin-bottom: 8px; font-size: 16px; font-weight: 500; color: $c-black; line-height: 1.25;}
.support .question .accordion .detail p{font-size: 16px; font-weight: 500; color: $c-gray-3; line-height: 1.25;}

.support .notice-detail .tit{padding: 24px 0;}
.support .notice-detail .tit dt{margin-bottom: 4px; font-size: 32px; font-weight: 700; line-height: 1.2; color: $c-black;}
.support .notice-detail .tit dd{font-size: 16px; line-height: 1.5; color: $c-gray-4;}

.support .notice-detail .con{padding: 32px 0; min-height: 528px; font-size: 16px; line-height: 1.5; color: $c-gray-4; border-top: 1px solid $c-gray-4; border-bottom: 1px solid $c-gray-6;;}
.support .notice-detail .con strong{display: inline-block; margin-bottom: 20px; font-size: 20px; line-height: 1.45; color: $c-gray-1;}

.support .btn-group .prev{margin-right: auto;}
.support .btn-group .next{margin-left: auto;}

@media all and (max-width:960px) {
  .support .content-box{min-height: auto;}
}

@media all and (max-width:720px) {
  .contents.support{padding: 0;}
  .contents.support .page-tit{padding: 0 6.67vw;}

  .contents.support .tab-line{margin-left: 0; width: 100%;}

  .support .notice-table{margin-top: -3.33vw;}
  .support .notice-table li + li{margin-top: 0;}
  .support .notice-table li a{display: block; padding: 3.33vw 6.67vw; font-size: 3.89vw; border-radius: 0;}
  .support .notice-table li a:hover{background-color: inherit;}
  .support .notice-table .tit{margin-bottom: 1.11vw; padding-right: 0;}
  .support .notice-table .date{margin-left: 0; font-size: 3.33vw;}

  .support .question .accordion{margin-bottom: 0; padding: 0 6.67vw;}
  .support .question .accordion.active{border-radius: 0;}
  .support .question .accordion .top button{flex-direction: column-reverse; justify-content: flex-start; align-items: flex-start; position: relative; padding: 3.33vw 0 3.33vw 6.11vw; font-size: 3.89vw;}
  .support .question .accordion .top button::before{left: 0; top: 3.61vw; width: 4.44vw; height: 4.44vw;}
  .support .question .accordion .top button::after{position: absolute; right: 0; top: 3.33vw; width: 6.67vw; height: 6.67vw;}
  .support .question .accordion .top .kind{margin-top: 1.11vw;}
  .support .question .accordion .top .kind::after{display: none;}
  .support .question .accordion .top .tit{max-width: 90%; font-weight: 500; color: $c-gray-3; text-align: left;}
  .support .question .accordion .detail{padding: 3.33vw 0 3.33vw 6.11vw; max-width: 90%;}
  .support .question .accordion .detail strong{margin-bottom: 1.67vw; font-size: 3.89vw;}
  .support .question .accordion .detail p{font-size: 3.89vw; line-height: 1.5; color: $c-gray-4;}

  .support .inquiry{padding: 0 6.67vw;}

  .support .notice-detail{padding: 0 6.67vw;}
  .support .notice-detail .tit{padding: 8.89vw 0 5.56vw;}
  .support .notice-detail .tit dt{margin-bottom: 1.67vw; font-size: 5.56vw; font-weight: 500;}
  .support .notice-detail .tit dd{font-size: 3.89vw;}

  .support .notice-detail .con{padding: 8.89vw 0; min-height: auto; font-size: 3.89vw;}
  .support .notice-detail .con strong{margin-bottom: 4.44vw; font-size: 4.44vw;}

  .support .btn-group .prev, .support .btn-group .next{display: none;}
}

//login
.login{}
.login .content-box{display: flex; align-items: stretch;}
.login .white-box{flex-shrink: 0; margin-right: 24px; width: 424px;}
.login .inp-list {gap: 12px;}

.login .sns-login{padding-top: 12px;}
.login .sns-login .txt{padding-bottom: 24px; font-size: 16px; font-weight: 500; color: #2E3337; text-align: center;}
.login .sns-login .sns-list{@include flex('space-between');}
.login .sns-login .sns-list li{text-align: center;}
.login .sns-login .sns-list li ::before{content: ''; display: block; margin-bottom: 8px; width: 60px; height: 60px; border: 1px solid #EDEDED; border-radius: 50%; background-color: #F8F8F8; background-repeat: no-repeat; background-size: 100% auto; background-position: center;}
.login .sns-login .sns-list li.kakao a::before{background-image: url("../img/common/ico_kakao.svg");}
.login .sns-login .sns-list li.naver a::before{background-image: url("../img/common/ico_naver.svg");}
.login .sns-login .sns-list li.facebook a::before{background-image: url("../img/common/ico_facebook.svg");}
.login .sns-login .sns-list li.google a::before{background-image: url("../img/common/ico_google.svg");}
.login .sns-login .sns-list li a{font-size: 14px; color: #2E3337;}

.login .link-txt{display: flex; flex-direction: column; text-align: center;}
.login .link-txt::before{content: ''; display: block; margin: 48px 0 40px; width: 100%; height: 1px; background-color: #D8DDE2;}
.login .link-txt .btn + .btn{margin-top: 16px;}

.login .info-box{padding-top: 76px; width: 100%; height: 825px; text-align: center; @include bg('content/img_login_bg.png'); word-break: keep-all;}
.login .info-box strong{font-size: 32px; font-weight: 700; color: $c-blue;}
.login .info-box .tit{margin-top: 10px; font-size: 48px; font-weight: 700; line-height: 1.13; color: $c-black;}

//join
.join .white-box{display: flex; flex-direction: column; margin: 0 auto; width: 700px; min-height: 854px;}
.join .white-box .page-btn{margin-top: auto; padding-bottom: 0;}


@media all and (max-width:960px) {
  .login .white-box{margin: 0 auto; width: 424px;}
  .login .info-box{display: none;}

  .join .white-box{width: 100%; max-width: 700px;}
}

@media all and (max-width:720px) {
  .login .white-box{flex-shrink: 0; width: 100%;}
  .login .inp-list {gap: 3.33vw;}

  .login .sns-login{padding-top: 2.22vw;}
  .login .sns-login .txt{display: none;}
  .login .sns-login .sns-list li ::before{margin-bottom: 1.67vw; width: 13.33vw; height: 13.33vw; border-width: 0.28vw;}
  .login .sns-login .sns-list li a{font-size: 3.89vw;}

  .login .link-txt::before{margin: 8.89vw 0 8.89vw; height: 0.28vw;}
  .login .link-txt .btn + .btn{margin-top: 4.44vw;}

  .join .white-box{height: calc(100vh - 26vw); min-height: auto;}
  .join .white-box .page-btn{padding-top: 8.89vw; padding-bottom: 6.67vw;}
}

// complete
.complete{margin: 120px auto;}
.complete-box{display: flex; align-items: center; justify-content: center; flex-direction: column; padding-bottom: 120px;}
.complete-box .title-con{margin-bottom: 32px;}
.complete-box .title-con dl{text-align: center;}
.complete-box .title-con dl dt{margin-bottom: 6px; font-size: 32px; font-weight: 700;}
.complete-box .title-con dl dd{font-size: 16px; color: #7C828B;}
.complete-box .title-con dl dd span{color: #3575F6; font-weight: 700;}
.complete-box .icon-img{width: 444px;}

.complete.join{}
.complete.join .page-btn{width: 408px;}
.complete.join .career-register{width: 408px; text-align: center;}
.complete.join .career-register::before{content: ''; display: block; margin: 4px 0 32px; width: 100%; height: 1px; background-color: #D8DDE2;}
.complete.join .career-register .txt{margin-bottom: 16px; word-break: keep-all; color: #7C828B;}

@media all and (max-width:720px) {
  .complete{margin: 22.22vw auto 6.67vw;}
  .complete-box{padding-bottom: 13.89vw;}
  .complete-box .title-con{margin-bottom: 14.44vw;}
  .complete-box .title-con dl dt{margin-bottom: 1.67vw; font-size: 6.67vw; letter-spacing: -0.1vw; word-break: keep-all;}
  .complete-box .title-con dl dd{font-size: 4.44vw;}
  .complete-box .icon-img{width: 100%;}

  .complete.join .page-btn{width: 100%;}
  .complete.join .career-register{display: none;}
}

//career
.contents.career{max-width: 796px;}
.career .content-box{padding: 32px 40px 20px; box-shadow: 0 3px 6px rgba(0,37,106,0.05)}
.career .content-box + .content-box{margin-top: 20px;}
.career .content-box .title{display: flex; align-items: center; margin-bottom: 18px;}
.career .content-box .title h3{font-size: 20px; font-weight: 500;}
.career .content-box .title p{margin-left: 10px;}
.career .content-box .content-list{display: flex; align-items: center; flex-wrap: wrap;}
.career .content-box .content-list > .inp-checkbox,
.career .content-box .content-list > .inp-radio{width: 33.33%;}
.career .content-box .content-list .inp-label{margin-bottom: 12px;}
.career .content-box .content-list .inp{width: 100%;}
.career .content-box .content-list .inp .inp-group{width: 100%;}

.career .main-career{position: relative; padding-bottom: 85px;}
.career .main-career .career-add-con{display: flex; align-items: center; justify-content: center; position: absolute; left: 0; bottom: 0; width: 100%; height: 61px; border-top: 1px solid #D8DDE2;}
.career .main-career .career-add-con button{display: flex; align-items: center; justify-content: center;}
.career .main-career .career-add-con button::before{content:""; display: block; margin-right: 6px; width: 20px; height: 20px; background: url("../img/common/ico_add_btn.svg") no-repeat center center / 100% auto;}
.career .main-career .inp-select{margin-top: 16px; width: 100%;}

.career .addr-select{width: 195px;}
.career .addr-select + .addr-select{margin-left: 12px;}

@media all and (max-width:960px) {
  .contents.career{max-width: none; width: 100%;}
  .career .content-box{padding: 32px 0 20px; box-shadow: none;}
  .career .main-career{padding-bottom: 0;}
  .career .main-career .career-add-con{position: relative; border-top: none;}
  .career .addr-select{width: calc(50% - 6px);}
}

@media all and (max-width:720px) {
  .contents.career{padding: 0;}
  .contents.career .page-tit{margin-bottom: 0; padding: 0 6.67vw;}
  .career .content-box{padding: 6.67vw 6.67vw 2.22vw; border-bottom: 4.44vw solid #F4F5F8; -webkit-border-radius: 0;-moz-border-radius: 0;border-radius: 0;}
  .career .content-box + .content-box{margin-top: 0;}
  .career .content-box .title{margin-bottom: 4.44vw;}
  .career .content-box .title h3{font-size: 4.44vw;}
  .career .content-box .title p{margin-left: 1.67vw; font-size: 3.33vw;}
  .career .content-box .content-list{}
  .career .content-box .content-list > .inp-checkbox,
  .career .content-box .content-list > .inp-radio{width: 100%;}
  .career .content-box .content-list .inp-label{margin-bottom: 4.44vw;}
  .career .content-box.addr-box{border-bottom: none;}

  .career .main-career{padding-bottom: 0;}
  .career .main-career .career-add-con{margin-top: 5.56vw; padding-bottom: 8.33vw; height: auto; font-size: 3.89vw;}
  .career .main-career .career-add-con button::before{margin-right: 1.11vw; width: 4.44vw; height: 4.44vw;}
  .career .main-career .inp-select{margin-top: 4.44vw;}

  .career .addr-select{width: calc(50% - 1.67vw);}
  .career .addr-select + .addr-select{margin-left: 3.33vw;}

  .career .page-btn{padding: 0 6.67vw;}
}

//employ
.employ-filter{@include flex();}
.employ-filter .left.pc_only{@include flex();}
.employ-filter .left.mo_only{display: none;}
.employ-filter .right{@include flex(); margin-left: auto;}

.employ-filter .left .inp-select .value{margin-left: auto; color: $c-blue;}
.employ-filter .left .inp-select + .inp-select{margin-left: 12px;}
.employ-filter .left .inp-select .inp-options{padding: 24px; width: 492px;}
.employ-filter .left .inp-select .btn-group .btn + .btn{margin-left: 12px;}
.employ-filter .left .inp-select .inp-btn::after{margin-left: 0;}

.employ-filter .region-select{@include flex('', 'flex-start'); height: 320px; font-size: 14px; border: 1px solid #D8DDE2; border-radius: 12px;}
.employ-filter .region-select .sub-tit{margin-bottom: 10px; font-weight: 500; line-height: 1.3; color: #2E3337;}
.employ-filter .region-select .city{overflow: hidden auto; flex: 1; padding: 16px 16px 0; height: 100%; border-right: 1px solid #D8DDE2;}
.employ-filter .region-select .city .inp-btn-list li button{@include flex(); width: 100%; padding: 6px 12px; color: #7C828B; text-align: left;}
.employ-filter .region-select .city .inp-btn-list li.active button{font-weight: 500; color: #2E3337; background-color: #F4F5F8; border-radius: 6px;}
.employ-filter .region-select .city .inp-btn-list li.active button::after{content: ''; display: inline-block; margin-left: auto; width: 16px; height: 16px; @include mask('common/icon_arrow_right.svg', #999);}
.employ-filter .region-select .district{overflow: hidden auto; flex: 1; padding: 16px 16px 0; height: 100%;}
.employ-filter .region-select .district .inp-checkbox .inp-label{margin-bottom: 0; padding: 6px;}

.employ-filter .region-select-list .sub-tit{margin: 20px 0 12px; font-size: 16px; font-weight: 700;}
.employ-filter .region-select-list .tag-list{@include flex(); flex-wrap: wrap; margin: 0 0 -12px -12px;}
.employ-filter .region-select-list .tag-list li{@include flex(); margin: 0 0 12px 12px; width: fit-content; padding: 0 8px 0 12px; height: 32px; font-size: 14px; font-weight: 500; color: $c-blue; background-color: #f5f8ff; border-radius: 16px;}
.employ-filter .region-select-list .tag-list li .btn-delete{@include flex('center'); margin-left: 8px; width: 16px; height: 16px; border-radius: 50%; background-color: #ACBBD8;}
.employ-filter .region-select-list .tag-list li .btn-delete::before{content: ''; display: inline-block; width: 100%; height: 100%; @include mask('common/ico_delete.svg', $c-white);}
.employ-filter .region-select-list .tag-list li .btn-delete:hover{background-color: #3575F6;}

.employ-filter .search{@include flex(); padding-bottom: 5px; margin-right: 24px; width: 260px; border-bottom: 1px solid #D8DDE2;}
.employ-filter .search:hover{border-color: #a3c2ff;}
.employ-filter .search:focus-within{border-bottom-width: 2px; border-color: #3575f6;}
.employ-filter .search .icon-search{flex-shrink: 0; margin-right: 8px; vertical-align: middle;}
.employ-filter .search input{width: 100%;}
.employ-filter .search input::placeholder{color: #B9C0C6;}
.employ-filter .search .btn-delete{display: none; align-items: center; flex-shrink: 0; margin-left: 8px; width: 20px; height: 20px; border-radius: 50%; background-color: #999;}
.employ-filter .search .btn-delete.active{display: flex;}
.employ-filter .search .btn-delete::before{content: ''; display: inline-block; width: 100%; height: 100%; @include mask('common/ico_delete.svg', $c-white);}

.employ-list{display: flex; flex-wrap: wrap; justify-content: center; margin: 24px 0 -16px;}
.employ-list .item{overflow: hidden; position: relative; margin: 0 1.25% 1.25% 0; width: 24%; background-color: $c-white; border: 1px solid #D8DDE2; border-radius: 20px; box-sizing: border-box; word-break: keep-all;}
.employ-list .item a{display: block; height: 100%;}
.employ-list .item:nth-child(4n){margin-right: 0;}
.employ-list .item .img-box{overflow: hidden; width: 100%; height: 110px; border-bottom: 1px solid #D8DDE2;}
.employ-list .item .img-box img{height: 100%; object-fit: cover;}
.employ-list .item .img-box .badge{position: absolute; top: 16px; right: 16px;}
.employ-list .item .txt-box{display: flex; flex-direction: column; padding: 16px 24px; height: calc(100% - 110px);}
.employ-list .item .txt-box .sort{margin-bottom: 8px; font-size: 16px; font-weight: 500; color: $c-blue;}
.employ-list .item .txt-box .tit{font-size: 20px; font-weight: 500; color: $c-black; line-height: 1.4;}
.employ-list .item .txt-box .desc{margin-top: auto; padding-top: 20px; font-size: 14px; color: #2E3337;}
.employ-list .item .txt-box .desc p::after{content: ''; display: block; margin: 10px 0 12px; width: 100%; height: 1px; background-color: #D8DDE2;}
.employ-list .item .txt-box .desc p:last-child::after{display: none;}
.employ-list .item .txt-box .desc span::after{content: ''; display: inline-block; margin: 0 8px; width: 1px; height: 12px; background-color: #D8DDE2;}
.employ-list .item .txt-box .desc span:last-child::after{display: none;}

.detail-info{display: flex; padding: 48px 0 80px;}
.detail-info > .info-box{margin-right: 10%; width: 100%; word-break: keep-all;}
.detail-info > .info-box.w701{margin: 0 auto; width: 701px;}
.detail-info > .info-box .tit-box{@include flex(); padding-bottom: 22px;}
.detail-info > .info-box .tit-box .img-logo{overflow: hidden; flex-shrink: 0; @include flex('center');  margin-right: 24px; width: 80px; height: 80px; border-radius: 16px; border: 1px solid #d8dde2; background-color: #f4f5f8;}
.detail-info > .info-box .tit-box .img-logo img{height: 100%; object-fit: contain;}
.detail-info > .info-box .tit-box .txt span{font-size: 20px; color: #2e3337;}
.detail-info > .info-box .tit-box .txt p{margin-top: 2px; font-size: 32px; font-weight: 700; color: $c-black;}

.detail-info .info-list dl{display: flex; margin-top: 32px; font-size: 16px; line-height: 1.25;}
.detail-info .info-list dl:first-child{margin-top: 0;}
.detail-info .info-list dt{margin-right: 45px; min-width: 82px; color: #7c828b;}
.detail-info .info-list dd{color: #2e3337;}
.detail-info .info-list dd.tag{padding: 10px 16px; color: $c-black; border-radius: 20px; background-color: #f4f5f8;}
.detail-info .info-list dd .notice-txt{display: block; margin-top: 6px; font-size: 14px; color: #7C828B;}
.detail-info .info-list dd .notice-txt + .notice-txt{margin-top: 2px;}
.detail-info .info-list dl.type2{margin-top: 12px;}
.detail-info .info-list dl.type2 dt{margin-right: 10px; min-width: 50px;}
.detail-info .info-list dl.type2 ~ .sub-txt{margin-top: 32px;}

.detail-info .default-info{margin-bottom: 32px; padding: 32px 0; border-top: 1px solid #d8dde2; border-bottom: 1px solid #d8dde2;}
.detail-info .default-info dl{align-items: center;}
.detail-info .default-info dl + dl{margin-top: 16px;}

.detail-info .add-info{font-size: 16px;}
.detail-info .add-info .tit{margin-bottom: 20px; font-size: 20px; font-weight: 700; color: #141618;}
.detail-info .add-info .sub-txt{font-size: 16px; font-weight: 500; color: $c-black;}
.detail-info .add-info .sub-txt + dl{margin-top: 16px; min-width: 80px;}
.detail-info .add-info .desc{margin-bottom: 16px; color: #7c828b;}
.detail-info .add-info .desc:last-of-type{margin-bottom: 0;}
.detail-info .add-info .hr{display: block; margin: 32px 0; width: 100%; height: 1px; border: 0; background-color: #ebebeb;}
.detail-info .add-info img{margin-top: 32px;}
.detail-info .add-info li + li{margin-top: 7px;}


.detail-info .dot li{position: relative; padding-left: 12px; line-height: 1.5;}
.detail-info .dot li::before{content: ''; display: block; position: absolute; top: 8px; left: 0;  width: 4px; height: 4px; background-color: #2e3337; border-radius: 50%;;}

.detail-info .inquiry{margin-top: 60px; padding: 32px; border: 1px solid #d8dde2; border-radius: 20px;}
.detail-info .inquiry .tit{margin-bottom: 24px; font-size: 16px; font-weight: 700; color: #141618;}
.detail-info .inquiry .txt-box{@include flex(); }
.detail-info .inquiry .img-box{overflow: hidden; margin-right: 24px; flex-shrink: 0; width: 80px; height: 80px; border-radius: 50%; background: #ddd; border: 1px solid #d8dde2;}
.detail-info .inquiry .info-box .name{margin-bottom: 8px; font-size: 16px; font-weight: 500; color: #141618;}
.detail-info .inquiry .info-box .name span{margin-left: 6px; color: #b9c0c6;}
.detail-info .inquiry .info-box .desc{font-size: 14px; color: #7c828b;}
.detail-info .inquiry .info-box .desc + .desc{margin-top: 2px;}

.detail-info .recruit-btn{position: sticky; top: 48px; right: 0; padding: 32px 20px 20px; flex-shrink: 0; width: 312px; height: fit-content; border-radius: 24px;  border: 1px solid #d8dde2;}
.detail-info .recruit-btn .txt-box{@include flex(); padding: 10px 10px 0; font-size: 16px;}
.detail-info .recruit-btn .txt-box p{color: #2e3337;}
.detail-info .recruit-btn .txt-box strong{margin-left: auto; font-weight: 500; color: $c-black;}
.detail-info .recruit-btn .txt-box strong ~ .badge{margin-left: 10px;}
