.form-con .addr-select{width: 195px;}
.form-con .addr-select + .addr-select{margin-left: 12px;}

.form-con .inp-address .inp-group.pc_only{display: flex;}
.form-con .inp-address .inp-group .inp-select{flex: 1; margin-right: 12px;}
.form-con .inp-address .inp-group .inp-select:last-child{margin-right: 0;}
.form-con .inp.textarea .inp-label .counter{margin-left: auto; font-size: 14px; color: #7c828b;}
.form-con .inp.textarea .inp-area{height: 128px;}

@media all and (max-width:960px) {
  .contents.program-apply{max-width: none; width: 100%;}
  .form-con .content-box{padding: 32px 0 20px; box-shadow: none;}
  .form-con .main-career{padding-bottom: 0;}
  .form-con .main-career .career-add-con{position: relative; border-top: none;}
  .form-con .addr-select{width: calc(50% - 6px);}
}

@media all and (max-width:720px) {
  .contents.program-apply{padding: 0;}
  .contents.program-apply .page-tit{margin-bottom: 0; padding: 0 6.67vw;}

  .form-con .content-box{padding: 6.67vw 6.67vw; border-bottom: 4.44vw solid #F4F5F8; -webkit-border-radius: 0;-moz-border-radius: 0;border-radius: 0;}
  .form-con .content-box.border-none{border-bottom: none;}
  .form-con .content-box + .content-box{margin-top: 0;}
  .form-con .title{margin-bottom: 4.44vw;}
  .form-con .title h3{font-size: 4.44vw;}
  .form-con .title p{margin-left: 1.67vw; font-size: 3.33vw;}
  .form-con .desc-box{margin-bottom: 4.44vw; padding: 4.44vw; height: fit-content; border-radius: 2.78vw;}
  .form-con .notice-txt{padding-left: 1.67vw; font-size: 3.33vw;}
  .form-con .notice-txt::before{top: 1.81vw; width: 0.56vw; height: 0.56vw;}

  .form-con .inp-list .inp-checkbox-group > .inp-label{margin-bottom: 3.61vw;}
  .form-con .inp-list .checkbox-list{margin-bottom: -4.72vw;}
  .form-con .inp-list .checkbox-list ~ .inp.etc{margin-top: 3.33vw;}
  .form-con .inp-list .inp-checkbox .inp-label{margin-bottom: 4.72vw;}
  .form-con .inp-list .inp-radio-group > .inp-label{margin-bottom: 3.61vw;}
  .form-con .inp-list .radio-list{margin-bottom: -4.72vw;}
  .form-con .inp-list .inp-radio .inp-label{margin-bottom: 4.72vw;}
  .form-con .inp.size-l .inp-area{height: 16.11vw;}
  .form-con .inp-label{word-break: keep-all;}

  .form-con .main-career{padding-bottom: 0;}
  .form-con .main-career .career-add-con{margin-top: 5.56vw; padding-bottom: 8.33vw; height: auto; font-size: 3.89vw;}
  .form-con .main-career .career-add-con button::before{margin-right: 1.11vw; width: 4.44vw; height: 4.44vw;}
  .form-con .main-career .inp-select{margin-top: 4.44vw;}

  .form-con .addr-select{width: calc(50% - 1.67vw);}
  .form-con .addr-select + .addr-select{margin-left: 3.33vw;}

  .form-con .inp-list .grid-col{grid-template-columns: auto;}
  .form-con .inp-address .inp-group.pc_only{display: none;}
  .form-con .inp-address .inp-group.mo_only{display: flex;}
  .form-con .inp-address .inp-group .inp-select{margin-right: 1.67vw;}
  .form-con .inp.textarea .inp-label .counter{font-size: 3.33vw;}
  .form-con .inp.textarea .inp-area{height: 31.11vw;}

  .form-con .page-btn{padding: 16px 6.67vw 0;}
}